


































import { Component, Vue, Provide } from "vue-property-decorator";
import EditDialog from './EditDialog.vue'
import { State,Getter,Mutation,Action } from 'vuex-class';


@Component({
  components: {
    EditDialog
  }
})
export default class TableData extends Vue {
  @Getter("user") getUser:any

  @Provide() searchVal: string = ""; //搜索
  @Provide() tHeight: number = document.body.offsetHeight - 270;
  @Provide() tableData: any = []; //表格数据
  @Provide() page: number = 1; //当前page
  @Provide() size: number = 5; //请求数据的个数 默认5
  @Provide() total: number = 0; // 总数据条数

  @Provide() dialogVisible: boolean = false ; //是否展示页面
  @Provide() formData: object ={
    title:"",
    type: '',
    level: '',
    count:"",
    date:""
  }

  loadData() {
    (this as any)
      .$axios(`/api/profiles/loadMore/${this.page}/${this.size}`)
      .then((res: any) => {
        // console.log(res.data)
        this.tableData = res.data.data.list;
        this.total = res.data.data.total;
      });
  }

//一页展示数量
  handleSizeChange(val: number): void {
    this.size = val;
    this.page = 1;
    this.searchVal ? this.loadSearchData() : this.loadData();
  }

//上一页或下一页
  handleCurrentChange(val:number):void{
    this.page = val;
    this.searchVal ? this.loadSearchData() : this.loadData();
  }

//搜索
  handleSearch():void{
    this.page = 1;
    this.searchVal ? this.loadSearchData() : this.loadData();
    
  }

//编辑
  handleEdit(index:number,row:any){
    // console.log(index,row)
    this.formData = row;
    this.dialogVisible = true;
  }

  closeDialog(){
    this.dialogVisible = false
  }

//删除
  handleDelete(index:number,row:any){
    (this as any).$axios.delete(`/api/profiles/delete/${row._id}`)
      .then((res:any) =>{
        this.$message({
          message:res.data.msg,
          type:'success'
        })

        this.tableData.splice(index,1)
      })
  }

  loadSearchData(){
    (this as any).$axios(`/api/profiles/search/${this.searchVal}/${this.page}/${this.size}`)
      .then((res:any)=>{
        this.tableData = res.data.datas.list;
        this.total = res.data.datas.total;
      })
  }

  created() {
    this.loadData();
  }
}
